<template>
  <div class="container-fluid p-0 float-right">
    <div class="row">
      <div class="col-md-12">
        <h1 class="h1 mb-0">Jadwal Kuliah {{ getDataProfile.full_name }}</h1>
        <br />
      </div>
      <!-- Line 1 -->
      <div class="col-12 col-md-12 float-right">
        <div
          class="card-deck"
          v-for="schedule in getDataJadwal"
          :key="schedule.name_ina"
        >
          <div
            class="card text-dark bg-ungu-accent mb-3 card-actions float-right"
            style="max-width: 19rem"
          >
            <div class="card-body">
              <h4 class="card-title">{{ schedule.name_ina }}</h4>
              <h6 class="card-title">
                {{ schedule.day_id }}
                {{ schedule.class_start_time.substring(1, 5) }} -
                {{ schedule.class_finish_time.substring(1, 5) }}
              </h6>
              Mahasiswa
              <p class="card-text">
                <img
                  src="img/avatars/avatar-2.jpg"
                  class="rounded-circle mr-1"
                  alt="Avatar"
                  width="28"
                  height="28"
                />
                <img
                  src="img/avatars/avatar.jpg"
                  class="rounded-circle mr-1"
                  alt="Avatar"
                  width="28"
                  height="28"
                />
                <img
                  src="img/avatars/avatar.jpg"
                  class="rounded-circle mr-1"
                  alt="Avatar"
                  width="28"
                  height="28"
                />
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "JadwalKuliah",
  data() {
    return {
      schedule: [],
    };
  },
  computed: {
    ...mapGetters({
      getDataJadwal: "allmatkul/allkuliah",
      getDataProfile: "profile/profile",
    }),
  },
  methods: {
    ...mapActions({
      profile: "profile/profile",
      jadwal: "allmatkul/allkuliah",
    }),
    getProfile() {
      this.profile();
    },
    getJadwal() {
      this.jadwal();
    },
  },
  mounted() {
    this.getProfile();
    this.getJadwal();
    $("title").html("Jadwal Kuliah | STTBANDUNG");
  },
};
</script>

<style>
.card-body {
  color: text-dark;
}
</style>